module.exports = {"patrons":[{"image":"patrons/microsoft.png","title":"Microsoft","link":"microsoft.com","url":"https://microsoft.com","content":"마이크로 소프트 소개문"},{"image":"patrons/jetbrains.png","title":"단군소프트","link":"jetbrains.com","url":"http://jetbrains.com","content":"JetBrains는 지능형 IDE 및 협업툴을 개발하는 소프트웨어 개발사 이며, 프라하(체코)에 본사를 두고, 러시아, 독일 및 미국에 지사를 운영하고 있습니다. \nJetBrains의 800명 이상의 직원들은 JAVA 생산성 향상 IDE인 IntelliJ IDEA 및 .NET 개발툴 ReSharper를 주력으로 한국 시장을 공략하고 있으며 포춘 500대 기업 중 431개 기업이 JetBrains의 IDE를 사용하고 있습니다.\nIT 업계는 시장 경쟁이 치열하며, 발빠른 시장 선점이 성공 비결의 핵심입니다. JetBrains 의 제품들은 프로그래머들이 불필요한 시간을 낭비하지 않도록 하여 기업의 생산성을 높이는데 획기적인 도움을 주고 있습니다."},{"image":"patrons/elastic.png","title":"ElasticSearch","link":"www.elastic.co","url":"https://www.elastic.co","content":"ElasticSearch 소개문"},{"image":"patrons/grapecity.png","title":"그레이프시티(주)","link":"grapecity.co.kr","url":"http://www.grapecit.co.kr","content":"그레이프시티는 전 세계 수 만명의 사용자를 보유한 세계 최대의 글로벌 UI 컴포넌트 개발사입니다.\nSpread(엑셀), SpreadJS(자바스크립트 엑셀), ComponentOne(종합 UI툴), Wijmo(자바스크립트 차트&그리드), ActiveReports(리포팅)등  다양한 솔루션을 통해 개발자들이 쉽고 빠르게 UI 개발을 할 수 있도록 돕고 있습니다. 특히, 다양한 기능, 윈폼-웹-모바일을 모두 아우르는 폭넓은 플랫폼, 감각적인 디자인, 압도적인 퍼포먼스는 그레이프시티의 큰 강점으로 꼽히고 있습니다.\n저희 그레이프시티 코리아는 동종 외산 개발툴 업계유일의 한국 지사를 통한 기술지원 서비스, 제품의 한글화, 커뮤니티 운영, 한글 데모 제공 등 다양한 방법을 통하여 한국 고객들을 위한 서비스 제공에 최선의 노력을 다하고 있습니다."},{"image":"patrons/oracle.jpg","title":"Oracle","link":"oracle.com","url":"http://oracle.com","content":"오라클 소개문"},{"image":"patrons/cubrid.jpg","title":"큐브리드","link":"cubrid.com","url":"http://www.cubrid.com","content":"CUBRID는 관계형 DBMS 제품으로 엔터프라이즈 시장에서 요구하는 대용량 데이터 처리 능력 및 성능, 안정성, 가용성, 관리 편의성을 제공하고 있습니다. 2008년 소스코드 공개 이후 국내외 30만건 이상의 제품 다운로드 실적을 보유하고 있으며, 국가정보자원관리원, 국방통합데이터센터,  네이버 등 600개 이상의 시스템에서 3,000여개 DB 인스턴스가 서비스 중에 있습니다."},{"image":"patrons/festa.png","title":"Festa","link":"festa.io","url":"https://festa.io","content":"Festa는 손쉽게 이벤트를 주최하고 등록할 수 있는 공간입니다. 복잡한 결제 시스템과 참가자 관리의 어려움을 해소하여 주최자와 참가자가 시간 낭비 없이 만날 수 있도록 돕고 있습니다. 국내 개발자 커뮤니티 모임이 성공적으로 진행될 수 있도록 적극 지원하고 있습니다."},{"image":"patrons/hanbit.png","title":"한빛미디어","link":"hanbit.co.kr","url":"http://www.hanbit.co.kr","content":"한빛 미디어 소개문"},{"image":"patrons/jpub.jpg","title":"제이펍출판사","link":"jpub.kr","url":"http://www.jpub.kr","content":"jpub 소개문"},{"image":"patrons/jiandson.png","title":"지앤선출판사","link":"jiandson.co.kr","url":"http://www.jiandson.co.kr","content":"지앤선 소개문"}]}

