module.exports = {"faqs":[{"title":"Workshop 참가를 원할 경우 어떻게 해야 하나요?","content":"spicalong@gmail.com 으로 참가하고자 하는 Workshop을 작성하시어 신청을 별도로 하여 주시기 바랍니다. 확인 후 별도로 준비사항을 공지할 예정입니다."},{"title":"참관비는 어떻게 납부 해야 하나요?","content":"참관비는 등록페이지를 통해 신용카드, 계좌이체, 무통장 입금으로 납부 가능합니다."},{"title":"세금계산서 발행이 가능한가요?","content":"컨퍼런스 종료 후 7일 이내 등록하신 이메일로 전자세금계산서가 일괄 발행됩니다. 단, 등록하실 때 사업자 등록정보를 입력하신 분에게 발행됩니다. 현장 등록시에도 세금계산서 발행 신청 가능하며 사업자등록증 사본을 제출하시면 세금계산서 일괄 방해시 함께 발행해 드립니다. \n\n* 부가가치세법 시행령에 따라 신용카드 결제 시, 세금계산서를 발행하지 않으니 참고하십시오."},{"title":"참가확인증 발급은 어떻게 되나요?","content":"참가확인증은 행사 당일 현장에서 접수합니다. 미처 발급을 못 받으신 경우에는 아래의 메일로 연락 주시기 바랍니다. spicalong@gmail.com Tel: 017-277-0917"},{"title":"법인 카드로 결제 시 어떻게 해야 하나요?","content":"인터넷으로는 법인카드 결제가 안 되는 경우가 있습니다. 불편하시겠지만 무통장으로 입금하여 주시기 바랍니다."},{"title":"행사당일 주차지원은 되나요?","content":"주차권은 제공되지 않으니, 가능하면 대중교통수단 이용을 부탁드립니다. 주말의 경우 근처 연합뉴스 건물에 하루종일 5천원에 주차가 가능합니다."},{"title":"점식식사는 제공이 되나요?","content":"참가신청 하신 분들 중 행사 당일 선착순 200분에게 점심식사를 제공합니다."},{"title":"사전 등록자 대신, 다른 사람이 참석해도 되나요?","content":"네. 등록하신 분의 명함을 지참하시고, 대리 참석하셔도 무방합니다."},{"title":"기타 질문사항이 있다면 어디에 문의하나요?","content":"spicalong@gmail.com 으로 메일 주시거나 또는 017-277-0917으로 연락 주시면 빠르게 답변드리도록 하겠습니다."}]}

